/* App.css */

.App {
  text-align: center;
  margin: 20px auto;
  padding: 20px;
  max-width: 400px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background-color: #f8f8f8;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

h1 {
  color: #333;
}

form {
  margin-top: 20px;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: #333;
}

input[type="text"],
input[type="tel"],
select {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 14px;
}

button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
  background-color: #007bff;
  color: #fff;
}

button[type="submit"] {
  background-color: #28a745;
}

button[type="reset"] {
  margin-left: 10px;
  background-color: #dc3545;
}

#paymentDetails {
  margin-top: 20px;
  padding: 10px;
  background-color: #d4edda;
  border-radius: 5px;
}

#paymentDetails p {
  color: #155724;
  font-size: 16px;
  line-height: 1.5;
}

select:disabled {
  background-color: #eee;
}

option:disabled {
  color: #aaa;
}
